import { Stack, Typography } from "@mui/material";
import { motion } from "framer-motion";

export default function Header(props) {
  return (
    <motion.div
      style={{
        height: "100vh",
        transition: "all 2s ease-in-out 0.5s" 
      }}
      className="centered"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
    >
      <Stack>
        <Typography variant='h1'> Hello, I am Suzette. </Typography>
        {props.children}
      </Stack>
    </motion.div>
  );
}
